//
import request from "../../utils/request";

export default {
  //
  reqRankList() {
    return request({
      method: "GET",
      url: "/rank",
    });
  },
  //
  reqRankDetail(bangId) {
    return request({
      method: "GET",
      url: "/rank/musicList",
      params: { bangId },
    });
  },
  //
  reqGetComments(sid, type) {
    return request({
      method: "GET",
      url: `/comment?sid=${sid}&type=${type}`,
      // params: { sid, type: aaa },
    });
  },
  //
  reqSongUrl(rid) {
    return request({
      method: "GET",
      url: `/url?rid=${rid}`,
    });
  },
};
