import request from "../../utils/request";

export default {
  //
  reqRecommendBanner() {
    return request({
      method: "GET",
      url: "/banner",
    });
  },
  //
  reqRecommendSong() {
    return request({
      method: "GET",
      url: "/rec_gedan",
    });
  },
  //
  reqRankList() {
    return request({
      method: "GET",
      url: "/rank/rec_bangList",
    });
  },
  //
  reqArtist() {
    return request({
      method: "GET",
      url: "/rec_singer",
    });
  },
  //
  reqRadio() {
    return request({
      method: "GET",
      url: "/radio",
    });
  },
};
